<template>
  <div>
    <div class="progress-bar">
      <div id="progressBar" style=""></div>
    </div>
    <div class="footer">
      <div class="footer__title">
        <p>{{ data[id].name }}</p>
        <p>{{ data[id].artist.name }}</p>
      </div>
      <div class="footer__nav">
        <router-link
          v-if="this.id >= 1"
          :to="{
            name: 'painting',
            params: { id: Number(this.$route.params.id) - 1 },
          }"
          class="previous"
        >
          <img
            src="https://6e222.csb.app/assets/shared/icon-back-button.svg"
            alt=""
            srcset=""
          />
        </router-link>

        <router-link
          v-if="this.id < 14"
          :to="{
            name: 'painting',
            params: { id: Number(this.$route.params.id) + 1 },
          }"
          class="next"
        >
          <img
            src="https://6e222.csb.app/assets/shared/icon-next-button.svg"
            alt=""
            srcset=""
          />
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import data from "../assets/data.json";
export default {
  name: "FooterComponent",
  methods: {
    progressBar: function () {
      var progressBar = document.getElementById("progressBar");
      var pertentage = parseInt(this.id) / 14;
      progressBar.style.width = pertentage * 100 + "%";
    },
  },
  created() {
    this.id = this.$route.params.id;
  },
  mounted() {
    this.progressBar();
  },
  data() {
    return {
      data: data,
      name: null,
      id: null,
    };
  },
};
</script>  