<template>
  <div>
    <div class="card">
      <div class="card-left">
        <picture>
          <source
            media="(min-width: 768px)"
            :srcset="publicURL + data[id].images.hero.large.substring(1)"
          />
          <source
            media="(max-width: 767px)"
            :srcset="publicURL + data[id].images.hero.small.substring(1)"
          />
          <img
            :src="publicURL + data[id].images.hero.large.substring(1)"
            :alt="data[id].name"
            class="painting-image"
          />
        </picture>
        <div class="view-image" type="button" @click="showModal">
          VIEW IMAGE
        </div>

        <div class="card-left__box">
          <h1 class="title">{{ data[id].name }}</h1>
          <h2 class="author">{{ data[id].artist.name }}</h2>
        </div>
      </div>
      <div class="card-right">
        <p class="display-year">{{ data[id].year }}</p>
        <p class="display-description">
          {{ data[id].description }}
          <br />
          <a :href="data[id].source" class="display-source" target="_blank"
            >GO TO SOURCE</a
          >
        </p>
      </div>
      <Modal v-show="isModalVisible" @close="closeModal" />
    </div>

    <FooterComponent></FooterComponent>
  </div>
</template>
 
<script>
import data from "../assets/data.json";
import Modal from "./Modal.vue";
import FooterComponent from "./FooterComponent.vue";

export default {
  name: "Painting",

  components: {
    Modal,
    FooterComponent,
  },
  created() {
    this.id = this.$route.params.id;
    this.publicURL = "https://6e222.csb.app";
  },
  data() {
    return {
      data: data,
      id: null,
      isModalVisible: false,

      image: null,
      images: null,
    };
  },

  methods: {
    showModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    },
  },
};
</script>
